

* {
  box-sizing: border-box;
}

.products {
    /* Add shadows to create the "products" effect */
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    display:inline-block;
    margin-left: auto;
    margin-right: auto;
    margin: 3rem;
    padding: 1rem;
    width:15rem;
    background-color:lightblue;
   /* border: 3px solid green; */
    animation: enter 1s ease-in-out;
    border-radius: 3rem;
  }

  /* On mouse-over, add a deeper shadow */
  .products:hover {
    box-shadow: 0 10px 16px 0 green;
    cursor: pointer;
  }
  
  /* Add some padding inside the products container */
  .container {
    padding: 2px 16px;
    text-align:center;
  }
  img {
    border-radius: 5px 5px 0 0;
    width: 100%;
  }


  .products button{
    border: 5px solid goldenrod;
    border-radius: 1rem;
    padding: 10px;
    margin-left: 20px;
    font-size: 15px
}


.products button:hover{
  background-color:bisque;
  cursor: pointer;
  border: 5px solid darkgoldenrod;
}




@keyframes enter {
  from {
     opacity: 0;
   filter: alpha(opacity=0);
  }

  to {
    filter: alpha(opacity=2);
    opacity: 2;
  }
}
